<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
transition-group(
  :appear="true"
  :class=`[
    "c-email-popover",
    "c-email-popover--" + direction,
    "c-email-popover--" + position,
    "c-email-popover--" + size,
    "c-email-popover--" + theme,
    "u-no-drag",
    "u-reset-widget"
  ]`
  enter-active-class="u-animated u-ms500 u-fade-in-left"
  leave-active-class="u-animated u-ms250 u-fade-out-left"
  tag="div"
)
  widget-close(
    v-if="triggered && closed === false"
    @close="onClose"
    class="c-email-popover__close"
    key="close"
    theme="light"
  )

  div(
    v-if="triggered && closed === false"
    class="c-email-popover__widget"
    key="widget"
  )
    .c-email-popover__body
      div(
        v-if="success ? imageLinkSuccess || imageLink : imageLink"
        :style=`{
          backgroundImage: 'url(' + (success ? imageLinkSuccess || imageLink : imageLink) + ')',
          height: imageHeight,
          width: imageWidth
        }`
        class="c-email-popover__image"
      )

      transition(
        enter-active-class="u-animated u-ms500 u-fade-in-right"
        leave-active-class="u-animated u-ms250 u-fade-out-left"
        mode="out-in"
      )
        div(
          v-if="success"
          class="c-email-popover__content"
        )
          p(
            v-html="messageContentSuccess"
            class="c-email-popover__message c-email-popover__message--success"
          )

          base-button(
            @click="onClose"
            :full-width="true"
            :size="size"
            color="green"
          ) Close

        .c-email-popover__content
          p(
            v-if="messageContent"
            v-html="messageContent"
            class="c-email-popover__message"
          )

          field-input(
            v-model="email"
            @submit="onSubmit"
            :autofocus="inputAutofocus"
            :error="error ? errorMessage : ''"
            :placeholder="inputPlaceholder"
            :size="size"
            :theme="theme"
            class="c-email-popover__input"
            left-icon="email"
          )

          base-button(
            @click="onSubmit"
            :full-width="true"
            :loading="submitting"
            :size="size"
          ) {{ buttonContent }}

          p(
            v-if="descriptionContent"
            v-html="descriptionContent"
            class="c-email-popover__description"
          )

    widget-branding(
      v-if="!premium"
      :theme="theme"
      class="c-email-popover__branding"
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// NPM
import { BaseButton, FieldInput } from "@growthbunker/vuedarkmode"

// PROJECT: COMPONENTS
import WidgetBranding from "@/components/widgets/WidgetBranding"
import WidgetClose from "@/components/widgets/WidgetClose"

// PROJECT: MIXINS
import CloseMixin from "@/mixins/CloseMixin"
import DirectionMixin from "@/mixins/DirectionMixin"
import PositionMixin from "@/mixins/PositionMixin"
import SizeMixin from "@/mixins/SizeMixin"
import ThemeMixin from "@/mixins/ThemeMixin"
import TriggerMixin from "@/mixins/TriggerMixin"
import WidgetMixin from "@/mixins/WidgetMixin"

export default {
  name: "EmailPopover",

  components: {
    BaseButton,
    FieldInput,
    WidgetBranding,
    WidgetClose
  },

  mixins: [
    CloseMixin,
    DirectionMixin,
    PositionMixin,
    SizeMixin,
    ThemeMixin,
    TriggerMixin,
    WidgetMixin
  ],

  props: {
    buttonContent: {
      type: String,
      default: "Join the mailing list"
    },
    descriptionContent: {
      type: String,
      default: null
    },
    imageHeight: {
      type: String,
      default: null
    },
    imageLink: {
      type: String,
      default: null
    },
    imageLinkSuccess: {
      type: String,
      default: null
    },
    imageWidth: {
      type: String,
      default: null
    },
    inputAutofocus: {
      type: Boolean,
      default: true
    },
    inputPlaceholder: {
      type: String,
      default: "Enter your email address"
    },
    messageContent: {
      type: String,
      default: null
    },
    messageContentSuccess: {
      type: String,
      default: "Congrats! You joined our mailing list."
    }
  },

  data: () => ({
    // --> STATE <--

    submitting: false,
    success: false,

    email: ""
  }),

  methods: {
    // --> EVENT LISTENERS <--

    async onSubmit() {
      if (!this.submitting) {
        this.submitting = true

        try {
          if (!this.editor) {
            this.$store.dispatch("sendData", {
              data: {
                email: this.email,
                from: this.widgetId,
                shop: "growth-bunker-julien.myshopify.com"
              }
            })
          }

          // Clear the email
          this.email = ""

          // Display a success message
          this.success = true
        } catch (error) {
          console.error(error)

          this.error = true
        }

        this.submitting = false
      }
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-email-popover";
$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  @include popover;
  @include popover-with-branding;
  @include popover-with-close;
  @include position;
  @include position-with-close;

  #{$c}__widget {
    #{$c}__body {
      display: flex;

      #{$c}__image {
        display: inline-block;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        user-select: none;
      }

      #{$c}__content {
        #{$c}__message {
          margin: 0 0 10px;

          &--success {
            text-align: center;
          }
        }

        #{$c}__input {
          box-sizing: border-box;
          margin-bottom: 10px;
          width: 100%;
        }

        #{$c}__description {
          margin: 10px 0 0;
          text-align: center;
        }
      }
    }
  }

  // --> DIRECTIONS <--

  &--horizontal {
    #{$c}__widget {
      #{$c}__body {
        flex-direction: row;

        #{$c}__image {
          flex: 0 0 auto;
          border-right-width: 1px;
          border-right-style: solid;
        }

        #{$c}__body {
          flex: 1;
        }
      }
    }
  }

  &--vertical {
    #{$c}__widget {
      #{$c}__body {
        flex-direction: column;

        #{$c}__image {
          border-bottom-width: 1px;
          border-bottom-style: solid;
        }
      }
    }
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      #{$c}__widget {
        #{$c}__body {
          #{$c}__content {
            padding: 8px + (2px * $i);
          }

          #{$c}__message {
            font-size: 12px + (1px * $i);
            line-height: 18px + (1px * $i);
          }

          #{$c}__description {
            font-size: 10px + (1px * $i);
            line-height: 10px + (1px * $i);
          }
        }
      }

      &#{$c}--horizontal {
        #{$c}__widget {
          #{$c}__body {
            #{$c}__image {
              width: 120px + (20px * $i);
            }
          }
        }
      }

      &#{$c}--vertical {
        #{$c}__widget {
          #{$c}__body {
            #{$c}__image {
              height: 100px + (20px * $i);
            }
          }
        }
      }
    }
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__widget {
        #{$c}__body {
          #{$c}__image {
            border-color: mdg($theme, "borders", "default", "primary");
          }

          #{$c}__content {
            #{$c}__message {
              &--success {
                color: mdg($theme, "statuses", "success");
              }
            }

            #{$c}__description {
              color: mdg($theme, "fonts", "default", "secondary");
            }
          }
        }
      }
    }
  }
}

// --> BREAKPOINT: 500PX <--

@include mq($from: 550px) {
  #{$c} {
    // --> SIZES <--

    @each $size in $sizes {
      $i: index($sizes, $size) - 1;

      &--#{$size} {
        &#{$c}--horizontal {
          width: 380px + (30px * $i);
        }

        &#{$c}--vertical {
          width: 280px + (20px * $i);
        }
      }
    }
  }
}
</style>
